.tariffs-data-header {
  background-color: $basic-color;
  color: $basic-white;
  padding: 12px 20px 30px;

  @media (min-width: $tablet-width) {
    padding: 12px 20px 12px;
  }

  @media (min-width: $desktop-width) {
    padding: 17px 20px 21px;
  }

  &--hit {
    position: relative;
  }
}

.tariffs-data-header__name {
  margin: 0;
  font-size: 16px;
  line-height: 30px;
  text-align: center;

  @media (min-width: $desktop-width) {
    font-size: 18px;
  }
}

.tariffs-data-header__price {
  margin: 0;
  font-weight: 300;
  font-size: 18px;
  line-height: 25px;
  text-align: center;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 30px;
  }
}

.tariffs-data-header__symbol {
  position: absolute;
  content: "";
  top: 0;
  left: 0;

  @media (min-width: $tablet-width) {
    width: 61px;
    height: 61px;
  }

  @media (min-width: $desktop-width) {
    width: 74px;
    height: 74px;
  }
}
