.page-header {
  width: 100%;
  margin: 0 auto;
  min-height: 733px;
  overflow: hidden;
  background-color: $basic-color;
  position: relative;
  background-image: url("../img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-position-x: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/back-mobile@2x.jpg");
    background-size: contain;
  }

  @media (min-width: $tablet-width) {
    min-height: 729px;
    background-image: url("../img/back-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/back-tablet@2x.jpg");
      background-size: cover;
    }
  }

  @media (min-width: $desktop-width) {
    min-height: 853px;
    background-image: url("../img/back-desktop.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/back-desktop@2x.jpg");
    }
  }

  // При открытии меню фон уходит вниз
  &--bg,
  &--no-js {
    background-position-y: center;

    @media (min-width: $tablet-width) {
      background-position-y: bottom;
    }
  }

  &--inner-background {
    position: relative;
    min-height: 236px;
    background-repeat: no-repeat;

    @media (min-width: $tablet-width) {
      min-height: 440px;
    }

    @media (min-width: $desktop-width) {
      min-height: 394px;
    }
  }
}

.page-header__container {
  background-color: rgba(0, 0, 0, 0.3);
  margin-bottom: 24px;
  min-height: 66px;

  @media (min-width: $tablet-width) {
    min-height: 110px;
    margin-bottom: 95px;
  }

  @media (min-width: $desktop-width) {
    min-height: 110px;
    margin-bottom: 100px;
  }

  &--inner {
    @media (min-width: $tablet-width) {
      margin-bottom: 65px;
    }
  }

  // При открытии меню меняется цвет шапки
  &--bg,
  &--no-js {
    background-color: $basic-color;
  }
}

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  @media (min-width: $tablet-width) {
    padding: 28px 0 0 0;
  }

  @media (min-width: $desktop-width) {
    flex-wrap: nowrap;
    padding: 25px 130px;
  }
}

.page-header__title {
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  color: $basic-white;
  margin-top: 30px;

  @media (min-width: $tablet-width) {
    font-size: 50px;
    line-height: 68px;
    padding: 0 130px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 150px;
  }
}

.page-header__slogan {
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  color: $basic-white;
  text-align: center;
  margin-top: 28px;

  @media (min-width: $tablet-width) {
    font-size: 50px;
    line-height: 68px;
    margin-top: 67px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 60px;
  }
}

.page-header__logo {
  padding-bottom: 20px;
  padding-left: 20px;
  margin-top: 20px;

  @media (min-width: $tablet-width) {
    padding-bottom: 43px;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 0;
    padding-left: 0;
  }

  //При отключенном js лого находится слева
  &--no-js {
    margin-right: auto;
  }
}
